// export default request;


 
import { getCache } from "@/utils/cache.js";
import { tansParams } from "@/utils/params.js";
import { redirectToLogin } from "@/common/navigation.js"; // 

// 
export function sendRequest(options) {
  const { url, method, data, params, headers = {} } = options; //  headers 

  //  URL 
  let requestUrl = url;
  if (params && (method === 'GET' || method === 'POST')) {
    requestUrl += '?' + tansParams(params);
  }

  // 
  const isToken = headers.isToken !== false;
  if (getCache("token") && isToken) {
    headers['Authorization'] = 'Bearer ' + getCache("token");
	headers['token'] = getCache("token");
    console.log("token");
  }

  return new Promise((resolve, reject) => {
    wx.request({
      url: requestUrl,
      method,
      data,
      header: headers,
      success: (res) => {
        const msg = res.data?.msg;
        if (msg && (msg.includes("token") || msg.includes(""))) {
          console.log("Token");
          redirectToLogin();
          reject(new Error("Token"));
          return;
        }
        resolve(res.data); //  data
      },
      fail: (err) => {
        reject(err);
      }
    });
  });
}



