import axios from "axios";
import {
	UniAdapter
} from "uniapp-axios-adapter";
import {
	getCache
} from "@/utils/cache.js";
import {
	tansParams
} from "@/utils/params.js";
import {
	redirectToLogin
} from "@/common/navigation.js"; // 

// 
const request = axios.create({
	// baseURL: "http://localhost:8089",
	// baseURL: "https://8.153.38.226:8089",
	// baseURL: "https://recluse.asia",
	baseURL: "https://fjk.iirl.top/stu-api",
	timeout: 1000000,
	adapter: UniAdapter,
	headers: {
		'Content-Type': 'application/json'
	}
});

request.interceptors.request.use((config) => {
	const isToken = (config.headers || {}).isToken !== false;

	// token
	if (getCache("token") && isToken) {
		config.headers['Authorization'] = 'Bearer ' + getCache("token");
		console.log("token");
	}

	// 
	if (config.params && config.method === 'post') {
		config.url = config.url + '?' + tansParams(config.params);
	}

	if (config.params && config.method === 'get') {
		config.url = config.url + '?' + tansParams(config.params);
	}

	return config;
});

request.interceptors.response.use(
	(response) => {
		const msg = response.data.msg;
		if (msg && msg.includes("token") || msg.includes("")) {
			// token
			console.log("Token");
			redirectToLogin(); // 
		}
		return response;
	});

export default request;
